.cardsContainer{
    display: flex;
    justify-content: space-evenly;
    gap: 20px;
    margin-top: 20px;
}
.home{
    background-color: whitesmoke;
    width: 100%;
    margin-top: 75px;
    border-radius: 8px;
    padding-bottom: 40px;
}

.cardBox{
    padding: 12px;
    list-style: none;
    display: flex;
    align-items: center;
    gap: 20px;
    background-color: white;
    border-radius: 8px;
}
.cardBox:hover{
    cursor: pointer;
}
.cardDetails{
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.cardLogo span{
    font-size: 30px;
    border-radius: 50%;
}
.cardDetails h4{
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-weight: bold;

}
.cardDetails p{
    color: rgb(83, 83, 83);
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 300;
}
.purple{
    color: purple;
}
.orange{
    color: orange;
}
.blue{
    color: blue;
}
.red{
    color: red;
}
.firstGraph{
    margin-top: 40px;
    /* margin-left: 10px; */
}
.grahpsAndProgressBar{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    gap: 20px;
}
.barsDetails{
    display: flex;
    gap: 20px;
    justify-content: center;
}
.secondBarDatails{
    display: flex;
    gap: 20px;
    /* margin-bottom: -7px; */
}
.secondProgressBar{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
    background-color: white;
    padding: 16px 26px;
    border-radius: 15px;
}

.progressBar{
    display: flex;
    flex-direction: column;
    gap: 16px;
    background-color: white;
    padding: 16px 15px;
    border-radius: 15px;
}

.secondGhraphs{
    display: flex;
    justify-content: space-evenly;
}

.apexcharts-toolbar{
    display: flex;
    justify-content: end;
    padding: 0;
}
.apexcharts-title-text{
    font-size: 13px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif !important;
}
.apexcharts-svg{
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 12px;
}
.apexcharts-toolbar{
    padding: 0 !important;
    justify-content: end !important;
    margin-top: 7px !important;
    gap: 0;
}


@media (min-width: 280px) and (max-width: 600px) {
    .cardsContainer{
        display: grid;
        grid-template-columns: 1fr 1fr;
        
    }
    .apexcharts-toolbar{
        padding: 0 !important;
        justify-content: end !important;
        margin-top: 7px !important;
        gap: 0;
    }

    .secondGhraphs{
        gap: 30px;
        margin-top: 50px;
    }
    .secondGhrapsContainer{
        display: flex;
        justify-content: center;
    }
    .secondGhraphs{
        display: flex;
        flex-direction: column;
    }
    .cards{
        display: flex;
        justify-content: center;

    }
    .cardDetails p{
        font-size: 9px;
        font-weight: 900;
    }
    .cardBox{
        width: 110px;
    }
    .grahpsAndProgressBar{
        display: flex;
        flex-direction: column;
    } 
    .home{
        width: 100%;
    }
.apexcharts-text tspan {
    font-size: 10px;
    
}
.apexcharts-datalabel{
    display: none !important;
}
.apexcharts-xaxis-texts-g{
    transform: translate(0) !important;
}
.firstGraph{
    margin-left: 0px;
}
}

@media (min-width: 601px) and (max-width: 1050px){
    .secondGhraphs{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 20px;
    }
    .grahpsAndProgressBar{
        display: grid;
        grid-template-columns: 1fr;
        gap: 10px;
        margin-left: 20%;
        margin-right: 20%;
    }
    .cardsContainer{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;

    }
    .cardBox{
        width: 150px;
    }
    .cards{
        display: flex;
        justify-content: center;
    }
}

