.header{
    padding: 15px;
    border-bottom: 0.5px solid black;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: white;
    z-index: 100;
}
.logoAndBtn{
    display: flex;
    gap: 20px;
}
.barsBtn{
    padding: 3px 12px;
    border: 0.5px solid black;
    color: black;
    background-color: white;
    border-radius: 6px;
}
.barsBtn:hover{
    cursor: pointer;
    background-color: whitesmoke;
}
.i{
    color: rgb(12, 94, 12);
    
}
.plus{
    color: rgb(162, 44, 162);
}
.logo{
    display: flex;
    gap: 15px;
    align-items: center;
    margin-left: 20px;
}
 
.i, .plus{
    font-size: 50px;
    font-weight: bold;
}
.logoPng{
    height: 35px;
}
.header{
    display: flex;
    justify-content: space-between;
}
.search{
    padding: 5px;
    outline: none;
    border: 0.5px solid black;
    border-radius: 8px;
    width: 300px;
}
.search::placeholder{
    padding-left: 20px;
    color: black;
}
.messegeSide{
    display: flex;
    align-items: center;
    gap: 20px;
    margin-right: 20px;
}
.userImage{
    height: 43px;
    width: 45px;
    border-radius: 50%;
}
.userDetails{
    display: flex;
    align-items: center;
    gap: 10px;
}
.userDetails p{
    font-size: 22px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 500;
}
.bellIconPng{
    height: 22px;
    width: 22px;
}
.containerPaths{
    display: flex;
}

.active{
    color: purple;
}

@media (min-width: 280px) and (max-width: 600px) {
    .search{
        width: 140px;
        padding: 13px;
    }
    .header{
        justify-content: space-between;
    }
    .logoAndBtn{
        gap: 8px;
    }
    .logoPng{
        height: 25px;
    }
    .logo p{
        font-size: 15px;
    }
    .messegeSide{
        display: none;
    }
    .logo{
        margin-left: 1px;
    }
    .home{
        width: 100%;
    }
}

@media (min-width: 600px) and (max-width: 1050px) {
    .search{
        width: 200px;
    }
    .logoAndBtn{
        gap: 4px;
    }
    .messegeSide{
        display: none;
    }
}