.location{
    width: 100%;
    margin-top: 75px;
    padding-bottom: 40px;
}

.locationAndDetails{
    display: flex;
    justify-content: space-evenly;
    margin-top: 20px;
}
.locatioinAndBenefits{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.benefitsPara{
    width: 100%;
    text-align: justify;
}
.benefitsBox{
    list-style: none;
    width: 542px;
}
.benefitsBox p{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.benefitsBox h1{
    width: 100%;
}
.benefitsContainer{
    display: flex;
    flex-direction: column;
    gap: 30px;
}

@media (min-width: 280px) and (max-width: 1030px) {
    .locationAndDetails{
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        gap: 40px;
    }
    .benefitsBox{
        width: 90%;
    }
    .benefitsContainer{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .locatioinAndBenefits{
        align-items: center;
        justify-content: center;
    }
    .benefitsBox h1{
        font-weight: bold;
    }
    .locationDetails p{
        width: 270px;
        text-align: justify;
    }
    .locationDetails h1{
        font-size: 30px;
        text-align: center;
    }
    .locationDetails{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

