/* AddPatientForm.css */

  
  .form-container {
    margin-top: 105px;
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 10px;
  }
  
  .patient-form {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    width: 100%;
  }
  
  .patient-form h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .input-group {
    margin-bottom: 15px;
  }
  
  .input-group label {
    font-size: 14px;
    color: #555;
  }
  
  .input-group input {
    width: 96%;
    padding: 10px;
    margin-top: 5px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 16px;
  }
  
  .submit-btn {
    width: 100%;
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .submit-btn:hover {
    background-color: #45a049;
  }
  
  @media (max-width: 600px) {
    .patient-form {
      padding: 15px;
    }
  
    .input-group input {
      padding: 5px;
      font-size: 14px;
    }
  
    .submit-btn {
      font-size: 14px;
    }
  }
  