.patients{
    margin-top: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-bottom: 30px;
}
.patientContainer{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    overflow: hidden;
}

.patientBox{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    height: 120px;
    color: rgb(49, 49, 49);
    border: 0.5px solid black;
    border-radius: 8px;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: whitesmoke;
}

.names{
    display: flex;
    flex-direction: column;
    padding-left: 20px;
    font-weight: bold;
    font-size: 17px;
    gap: 5px;

}
.age h4{
    padding-left: 20px;
}
.disease h2{
    padding-left: 20px;
    font-size: 18px;
}


@media  (min-width: 280px) and (max-width: 450px) {
    .patientContainer{
        display: grid;
        grid-template-columns: 1fr;
    }
    .patientsHeading{
        padding: 20px 1px;
        font-size: 25px;
    }
    
}

@media  (min-width: 451px) and (max-width: 750px) {
    .patientContainer{
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}

@media  (min-width: 751px) and (max-width: 930px) {
    .patientContainer{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
    }
}
