.sidebar{
    width: 160px;
    animation: sidebarAnimation 0.3s ease-in 0s 1 forwards;
    
}
.listContainer{
    background-color: white;
    display: flex;
    flex-direction: column;
    /* justify-content: space-evenly; */
    gap: 50px;
    height: 100vh;
    padding-top: 40px;
}
.listOptions{
    list-style: none;
    display: flex;
    gap: 10px;
    text-align: center;
    border-radius: 9px;
    margin-left: 30px;
    text-decoration: none;
    color: black;
    animation: listOptionsAnimation 2s ease 0s 1 forwards;
    

}
.listOptions:hover{
    cursor: pointer;
}

.active{
    color: purple;
}

.sidebar{
    z-index: 3;
    position: fixed;
    left: 0;
    top: 74px;
}

@keyframes sidebarAnimation {
    from{
        width: 0px;
    } to {
        width: 160px;
    }
    
}

@keyframes listOptionsAnimation {
    from{
        opacity: 0;
    } to{
        opacity: 100;
    }
    
}
